// src/lib/utils.js
import products from '@/data/products.json';
import categories from '@/data/categories.json';

/**
 * Obtém todos os produtos
 * @returns {Array} Lista de produtos
 */
export function getProducts() {
  return products;
}

/**
 * Obtém todos as categorias
 * @returns {Array} Lista de categorias
 */
export function getCategories() {
  return categories;
}

/**
 * Obtém produtos filtrados por categoria
 * @param {string} categorySlug - Slug da categoria
 * @returns {Array} Produtos da categoria especificada
 */
export function getProductsByCategory(categorySlug) {
  return products.filter(product => product.category === categorySlug);
}

/**
 * Obtém uma categoria pelo slug
 * @param {string} slug - Slug da categoria
 * @returns {Object|null} Categoria encontrada ou null
 */
export function getCategoryBySlug(slug) {
  return categories.find(category => category.slug === slug) || null;
}
