// src/app/categoria/[slug]/page.js
import { getProductsByCategory, getCategoryBySlug } from '@/lib/utils';
import Link from 'next/link';
import Image from 'next/image';
import { notFound } from 'next/navigation';

export default async function CategoryPage({ params }) {
  const { slug } = params;
  
  // Busca dados assincronamente
  const [category, products] = await Promise.all([
    getCategoryBySlug(slug),
    getProductsByCategory(slug)
  ]);

  // Retorna 404 se categoria não existir
  if (!category) {
    notFound();
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Cabeçalho da Categoria */}
      <div className="mb-10 text-center">
        <h1 className="text-4xl font-extrabold text-gray-900 mb-3">{category.name}</h1>
        {category.description && (
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            {category.description}
          </p>
        )}
      </div>

      {/* Listagem de Produtos */}
      {products.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
          {products.map((product) => (
            <ProductCard key={product.id} product={product} />
          ))}
        </div>
      ) : (
        <div className="text-center py-20">
          <p className="text-gray-500 text-lg">
            Nenhum produto disponível nesta categoria no momento.
          </p>
        </div>
      )}
    </div>
  );
}

// Componente de Card de Produto separado para melhor organização
function ProductCard({ product }) {
  return (
    <Link 
      href={`/produto/${product.id}`}
      className="group relative bg-white border border-gray-200 rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-all duration-300"
    >
      {/* Imagem do Produto */}
      <div className="aspect-square bg-gray-100 relative overflow-hidden">
        <Image
          src={`/images/products/${product.image}`}
          alt={product.name}
          fill
          className="object-cover group-hover:scale-105 transition-transform duration-300"
          sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw"
          priority={false}
        />
      </div>

      {/* Informações do Produto */}
      <div className="p-4">
        <h3 className="font-medium text-gray-900 text-lg mb-1 line-clamp-1">
          {product.name}
        </h3>
        <div className="flex justify-between items-center mt-3">
          <span className="text-lg font-bold text-gray-900">
            R$ {product.price.toFixed(2).replace('.', ',')}
          </span>
          <span className="inline-block bg-gray-100 text-gray-600 text-xs px-2 py-1 rounded">
            {product.category}
          </span>
        </div>
      </div>
    </Link>
  );
}
